<template>
  <div>
	<el-row style="margin-bottom:20px;">
	    <el-card shadow="always">
	    	<span class="content-title">首页轮播图管理</span>
			<el-button type="success" @click="openAddDialog()">新增</el-button>
			<!--<el-button type="text" style="float: right; padding: 3px 0" @click="moreSelect"><i :class="selectClass"></i>更多选项</el-button>-->
			<div class="advanced-box" v-if="content_show">
				<el-form :inline="true" :model="formBanner" class="demo-form-inline">
					<el-form-item label="标题">
						<el-input v-model="formBanner.title"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="onSubmit">查询</el-button>
					</el-form-item>
					<el-form-item>
						<el-button @click="reset">重置</el-button>
					</el-form-item>
				</el-form>
			</div>
	    </el-card>
	</el-row>
	<el-table
		border
		:data="tableData"
	>
		<el-table-column
			prop="index"
			label="序号"
			align="center"
			width="60">
		</el-table-column>
		<el-table-column
			prop="title"
			label="标题"
			align="center">
		</el-table-column>
		<el-table-column
			prop="img_show"
			label="banner图"
			align="center"
			width="130">
			<template slot-scope="scope">
		      <img  :src="scope.row.img_show" alt="" style="width: 50px;height: 50px">
		    </template>
		</el-table-column>
		<el-table-column
			prop="status_name"
			label="状态"
			align="center"
			width="90">
		</el-table-column>
		<el-table-column
			prop="banner_type_name"
			label="系统类型"
			align="center"
			width="90">
		</el-table-column>
		<el-table-column
			prop="release_time"
			label="发布时间"
			align="center"
			width="220">
		</el-table-column>
		<el-table-column
			prop="update_time"
			label="最后修改时间"
			align="center"
			width="220">
		</el-table-column>
		<el-table-column
			prop="update_user"
			label="责任编辑"
			align="center"
			width="130">
		</el-table-column>
		<el-table-column
			label="操作"
			align="center"
			width="300">
			<template slot-scope="scope">
		        <el-button
		          size="mini"
		          type="primary"
		          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
		        <el-button
		          size="mini"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
		        <el-button
		          size="mini"
		          @click="handleCancelShow(scope.$index, scope.row)">取消显示</el-button>
		    </template>
		</el-table-column>
	</el-table>
	<div class="block pager">
	  <el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page.sync="currentPage"
	      :page-sizes="[15, 20, 30]"
	      :page-size="page_size"
	      layout="sizes, prev, pager, next"
	      :total="total">
	  </el-pagination>
	</div>
	<el-dialog
	  title="编辑"
	  :visible.sync="dialogVisible"
	  width="50%"
	  top="5vh"
	  :append-to-body='true'>
	  <div class="el-dialog-banner">
	  <el-form label-position="top" :rules="rules" ref="formLabelAlign" label-width="80px" :model="formLabelAlign">
	  	  <el-form-item label="系统类型" prop="banner_type">
		    <el-radio-group v-model="formLabelAlign.banner_type">
		      <el-radio :label="1">PC端</el-radio>
		      <el-radio :label="2">移动端</el-radio>
		    </el-radio-group>
		  </el-form-item>
		  <el-form-item label="标题" prop="title">
		  	<el-col :span="23">
		    	<el-input v-model="formLabelAlign.title"></el-input>
		    </el-col>
		  </el-form-item>
		  <el-form-item label="副标题">
		  	<el-col :span="23">
		    	<el-input v-model="formLabelAlign.summary"></el-input>
		    </el-col>
		  </el-form-item>
		  <el-form-item label="banner图 图片建议尺寸:600×400" prop="img_name">
		  	<el-col :span="20">
		    	<el-input v-model="formLabelAlign.img_name" :disabled="true"></el-input>
		    </el-col>
		    <el-upload
			  class="upload-demo"
			  :data="uploadData"
			  action="core/file/upload"
			  multiple
			  :limit="1"
			  :before-upload="beforeUpload"
			  :show-file-list='false'>
			  <el-button size="medium" type="primary">点击上传</el-button>
			</el-upload>
		  </el-form-item>
		  <el-form-item label="链接">
		  	<el-col :span="23">
		    	<el-input v-model="formLabelAlign.img_link"></el-input>
		    </el-col>
		  </el-form-item>
		  <el-form-item label="发布时间" prop="release_time">
		    <el-date-picker
		      v-model="formLabelAlign.release_time"
		      type="datetime"
		      value-format="yyyy-MM-dd HH:mm:ss"
		      placeholder="选择日期"
		      default-time="01:01:01">
		    </el-date-picker>
		  </el-form-item>
		  <el-form-item label="修改时间" prop="update_time">
		  	<el-col :span="23">
		    	<el-input v-model="formLabelAlign.update_time" :disabled="true"></el-input>
		    </el-col>
		  </el-form-item>
		  <!--<el-form-item label="责任编辑">
		  	<el-col :span="23">
		    	<el-input v-model="formLabelAlign.update_user"></el-input>
		    </el-col>
		  </el-form-item>-->
	  </el-form>
	  </div>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="dialogVisible = false">取 消</el-button>
	    <el-button type="primary" @click="addBannerInfo('formLabelAlign')">确 定</el-button>
	  </span>
	</el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import { authToken } from '@/config';
import { storageHelper,getCurTime,utcTimeChange } from '@/utils';
export default {
	data() {
      return {
      	token:'',
        selectClass:"el-icon-arrow-down",
        content_show:false,
        formBanner: {
        	title: ''
        },
        //表格
        tableData: [],
        currentPage:1,
		page_size:15,
		total:0,
		//弹出框
		dialogVisible: false,
		//表单
		formLabelAlign: {
          title: '',
          summary: '',
          img: '',
          img_name:'',
          img_show:'',
          img_link: '',
          status: 1,
          status_name:'',
          banner_type: 1,
          release_time: '',
          update_time: '',
          update_user: '',
        },
        rules: {
          title: [{ required: true, message: '请输入标题', trigger: 'blur' }],
          img_name:[{ required: true, message: '请上传图片', trigger: 'blur' }],
          img_link:[{ required: true, message: '请输入链接地址', trigger: 'blur'}],
          release_time:[{ required: true, message: '请选择时间', trigger: 'blur'}],
          update_user:[{ required: true}],
        },
        uploadData: {
	        module: 'banner'
	    },
      };
    },
    mounted() {
    	this.token = storageHelper.getItem(authToken, 'healthcare');
    	this.getBannerList('1','15');
    },
    methods: {
      openAddDialog(){
      	this.formLabelAlign = {
          title: '',
          img: '',
          summary: '',
          img_name:'',
          img_show:'',
          img_link: '',
          status: 1,
          status_name:'',
          banner_type: 1,
          release_time: '',
          update_time: getCurTime(),
          update_user: '',
        },
      	this.dialogVisible = true;
      },
      beforeUpload(file){
      	var that =this;
      	var fd = new window.FormData();
	    fd.append('file', file);
	    axios.post("core/file/upload", fd, {
	        headers: {
	        	'Content-Type':'multipart/form-data',
	            token: storageHelper.getItem(authToken, 'healthcare')
	        }
	    }).then(function (res) {
	    	if(res.code == 0){
	    		var url = res.data.url;
	    		that.formLabelAlign.img_name = file.name;
	    		that.formLabelAlign.img = res.data.code;
	    		that.$message({
		            type: 'success',
		            message: '上传成功！'
		        });
	    	}
	      //console.log(res);
	      
	    });
	    return false // 返回false不会自动上传
      },
      addBannerInfo(formType){
      	var that = this;
      	this.$refs[formType].validate(valid => {
	        if (valid) {
	        	that.dialogVisible = false;
	        	var method = '';
	        	var info = '';
	        	if(that.formLabelAlign.hasOwnProperty("banner_id"))
	        	{
	        		method = 'content/banner/update';
	        		info = '修改成功!';
	        	}else{
	        		method = 'content/banner/create';
	        		info = '添加成功!';
	        	}
	        	that.submitBannerInfo(method,that.formLabelAlign,info);
	        } else {
	          return false;
	        }
	    });
      },
      getBannerList(page_index,page_size){
      	var params = {page_index:page_index,page_size:page_size};
      	var that = this;
		axios.get('content/banner/pageList', {params}).then(function(response){
			var code = response.code;
			if(0 == code){
				var array = [];
				var rdata = response.data;
				var count = rdata.count;
				var page_index = rdata.page_index;
				var page_size = rdata.page_size;
				that.currentPage = page_index;
				that.page_size = page_size;
				that.total = count;
				var des_num = (page_index - 1) * page_size;
				var json = rdata.rows;
				for(var i = 0;i < json.length;i++){
					var banner_type = json[i].banner_type;
					var status = json[i].status;
					json[i].release_time = utcTimeChange(json[i].release_time);
					json[i].update_time = utcTimeChange(json[i].update_time);
					var update_user = json[i].update_user;
					var banner_type_name = "PC端";
					if(banner_type == 2){
						banner_type_name = "移动端";
					}
					var status_name = "显示";
					if(status == 0){
						status_name = "不显示";
					}
					if(null != json[i].img_info){
						json[i].img_name = json[i].img_info.name;
						json[i].img_show = json[i].img_info.src;
					}
					json[i].banner_type_name = banner_type_name;
					json[i].status_name = status_name;
					json[i].index = des_num + i + 1;
					array.push(json[i]); 
				}
				that.tableData = array;
			}else{
				//console.log(code);
			}
		},function(error){
			//console.log(error);
		});
      },
      handleSizeChange(val) {
        this.getUserList(this.currentPage,val);
      },
      handleCurrentChange(val){
      	this.getUserList(val,this.page_size);
      },
      moreSelect(){
      	if(this.selectClass=="el-icon-arrow-down"){
      		this.selectClass = "el-icon-arrow-up";
      		this.content_show = true;
      	}else{
      		this.selectClass = "el-icon-arrow-down";
      		this.content_show = false;
      	}
      },
      onSubmit() {
        console.log('submit!');
      },
      reset() {
        console.log('reset!');
      },
      handleEdit(index, row) {
        this.dialogVisible = true;
        this.formLabelAlign = {
          banner_id: row.banner_id,
          title: row.title,
          img: row.img,
          summary: row.summary,
          img_name: row.img_name,
          img_show: row.img_show,
          img_link: row.img_link,
          status: row.status,
          status_name: row.status_name,
          banner_type: row.banner_type,
          release_time: row.release_time,
          update_time: row.update_time,
          update_user: row.update_user,
        };
      },
      handleDelete(index, row) {
      	var that = this;
      	this.$confirm('是否确认删除该条信息?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        	var params = {banner_id:row.banner_id};
        	that.submitBannerInfo('content/banner/delete',params,'删除成功!');
        }).catch(() => {
        });
      },
      handleCancelShow(index, row) {
        var params = {banner_id:row.banner_id};
        that.submitBannerInfo('content/banner/updateStatus',params,'取消显示成功!');
        console.log(row.banner_id);
      },
      submitBannerInfo(method,params,info){
      	var that = this;
		axios.post(method, params).then(function(response){
			var code = response.code;
			if(0 == code){
				that.$message({
		            type: 'success',
		            message: info
		        });
				that.getBannerList(that.currentPage,that.page_size);
			}else{
				that.$message({
		            type: 'success',
		            message: '操作失败:' + response.message
		        });
			}
		},function(error){
		});
      },
    }
};
</script>

<style scoped>
.content-title{
	margin-right:20px;
}
.advanced-box{
	padding:20px 10px;
	margin-top:15px;
	background-color:#f7f7f7;
}
.pager{
	float:right;
	padding:10px 0;
}
.el-dialog__header{
	border-bottom: 1px solid #CCC;
}
.el-dialog-banner{
	height:530px !important;
	padding-right:15px;
	overflow-y:scroll;
}
.el-dialog__footer{
	border-top: 1px solid #CCC;
}
.el-form-item__label{
	padding-bottom:0 !important;
}
</style>